<template>
	<view class="container">
		<view class="bg"></view>
		<view class="form">
			<view class="header">
				<view class="title">人工智能回答你需要的问题</view>
			</view>
			<u-transition :show="true" mode="fade-left">
				<view class="panel">
					<view class="head">
						<view class="title" style="display: flex;line-height: 34rpx;">
							<view>{{chatSence.tips}}</view>
							<view v-if="[3,5].includes(chatSence.scene)">
								<u--text :text="language" @click="langShow = true" decoration="underline"
									suffixIcon="arrow-right" color="#26B3A0" :customStyle="{'margin-left':'4rpx'}"
									:iconStyle="{'color':'#26B3A0','font-size':'24rpx'}">
								</u--text>
								<u-picker :show="langShow" :columns="langColumns" :defaultIndex="[1]"
									@confirm="setLang" @cancel="langShow = false"
									:closeOnClickOverlay="true">
								</u-picker>
							</view>
						</view>
						<view class="tips">
							<u-button text="清空问题" size="mini" iconColor="#f56c6c" color="#fef0f0" icon="trash"
								:customStyle="{color:'#f56c6c'}" @click="resetQuestion">
							</u-button>
						</view>
					</view>
					<view class="textarea">
						<u--textarea v-model="question" height="160" :placeholder="chatSence.placeholder"
							confirmType="done" count maxlength="280">
						</u--textarea>
					</view>
					<view class="btn-group">
						<view class="btn paste">
							<u-button @click="distinguish" icon="file-text" text="看图识字"></u-button>
						</view>
						<view class="btn get">
							<u-button @click="onSubmitGPT" :loading="gptLoading" iconColor="#ffffff" color="#26B3A0"
								icon="edit-pen" :text="chatSence.btnText"></u-button>
						</view>
					</view>
				</view>
			</u-transition>
		</view>
		<view class="alert">
			<u-alert title="当前使用人数过多,获取答案可能超过30秒钟" type="error"></u-alert>
		</view>
		<view class="tool">
			<view class="header">
				<view class="title">预设场景</view>
			</view>
			<u-transition :show="true" mode="slide-up">
				<view class="grid">
					<block v-for="(item,key) in sceneData" :key="key">
						<view class="item" :class="[(sceneDataSelected == key ? 'item-selected':'')]"
							@click="resetScene(key)">
							<view class="icon">
								<u-icon size="28" :name="item.icon"></u-icon>
							</view>
							<view class="title">{{item.title}}</view>
							<view class="desc">{{item.desc}}</view>
						</view>
					</block>
				</view>
			</u-transition>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				question: '',
				gptLoading: false,
				sceneDataSelected: 0,
				sceneData: this.$configData.chatSence,
				chatSence: {},
				language: '英语',
				langShow: false,
				langColumns: [
					['中文', '英语', '日语', '韩语']
				],
			};
		},
		onShareAppMessage(res) {
			return {
				title: 'AI问答：解锁您问题的答案！',
				path: '/pages/index/index',
			}
		},
		onShareTimeline(res) {
			return {
				title: 'AI问答：解锁您问题的答案！',
				path: '/pages/index/index',
			}
		},
		onLoad() {
			this.resetScene(this.sceneDataSelected);
		},
		methods: {
			checkToken() {
				const token = uni.getStorageSync('token');
				if (token == '' || token == undefined) {
					return false
				} else {
					return true
				}
			},
			resetQuestion() {
				this.question = '';
			},
			// 预设场景切换
			resetScene(index) {
				this.sceneDataSelected = index;
				if (this.sceneDataSelected == 5) {
					return false;
				}
				this.chatSence = this.sceneData[index];
			},
			setLang(e) {
				this.language = e.value[0];
				this.langShow = false;
			},
			onSubmitGPT() {
				//检测用户是否登录
				let islogin = this.checkToken();
				if (!islogin) {
					uni.navigateTo({
						url: '/pages/login/index'
					});
					return false;
				}
				this.gptLoading = true;
				//已经登录，则调用后端接口数据
				let question = this.chatSence.question.replace("{lang}", this.language);
				question = question.replace("{question}", this.question);
				let postData = {
					question: question
				};
				this.$http.requestApi('POST', 'chatgpt/question/answer', postData).then(res => {
					const resCode = res.status;
					this.gptLoading = false;
					if (resCode == 200) {
						uni.navigateTo({
							url: '/pages/detail/index?avg=' + res.data.qaId
						});
					} else {
						uni.showToast({
							title: res.msg,
							duration: 2000,
							icon: 'none'
						});
					}
				})
			},
			//从相册或拍照选择
			async distinguish(type) {
				//检测用户是否登录
				let islogin = this.checkToken();
				if (!islogin) {
					uni.navigateTo({
						url: '/pages/login/index'
					});
					return false;
				}
				let sourceType = [];
				if (type == 'album') {
					sourceType = ['album']
				}
				if (type == 'camera') {
					sourceType = ['camera']
				}
				let _this = this
				uni.chooseImage({
					count: 1,
					success(res) {
						_this.original_img = res.tempFilePaths[0]; //选择图片返回的相对路径,
						_this.getImgInfo(_this.original_img);
					}
				})
			},
			getImgInfo(img) {
				let _this = this
				uni.getImageInfo({
					src: img,
					success(imageInfo) {
						var imgType = imageInfo.type
						uni.getFileSystemManager().readFile({
							filePath: img,
							encoding: "base64", //这个是很重要的
							success: res => { //成功的回调
								//返回base64格式
								var base64Str = 'data:image/' + imgType + ';base64,' + res.data
								const postData = {
									img: base64Str,
									foo: 'accurateBasic'
								}
								_this.$http.requestApi('POST', 'chatgpt/img/toText', postData).then(
									res => {
										if (res.status == 200) {
											//调用成功
											_this.question = res.data.deal_data;
										} else {
											//调用失败
											uni.showToast({
												icon: "none",
												title: res.data.msg,
											})
										}
									});
							}
						})
					}
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	.share {
		position: fixed;
		right: 40rpx;
		bottom: 120rpx;
		width: 40%;

		.u-button {
			box-shadow: 0rpx 10rpx 10rpx #eee !important;
		}
	}

	.tool {
		width: 90%;
		margin: 15rpx auto;

		.header {
			.title {
				margin-bottom: 30rpx;
				font-weight: bold;
				font-size: 32rpx;
			}
		}

		.grid {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between;

			.item-selected {
				background-color: #F0FAF8;
			}

			.item {
				width: 30%;
				display: flex;
				flex-direction: column;
				align-items: center;
				margin: 15rpx 0rpx;
				padding: 30rpx 0rpx;
				border-radius: 10rpx;
				border: 1rpx solid #F0FAF8;

				.title {
					color: #1B2B20;
					margin-top: 15rpx;
				}

				.desc {
					color: #67776C;
					font-size: 24rpx;
				}
			}
		}
	}

	.alert {
		width: 90%;
		margin: 30rpx auto;
	}

	.form {
		width: 90%;
		margin: 0 auto;
		z-index: 999;

		.header {
			margin-bottom: 30rpx;

			.title {
				text-align: center;
				color: #fff;
				margin: 30rpx 0rpx;
			}
		}

		.foot {
			margin-top: 30rpx;
		}

		.panel {
			padding: 30rpx;
			background-color: #fff;
			border-radius: 15rpx;
			box-shadow: 0rpx 10rpx 10rpx #eee;

			.head {
				display: flex;
				flex-direction: row;
				justify-content: space-between;

				.tips {
					color: #dd6161;
				}
			}

			.textarea {
				margin-top: 30rpx;
			}

			.btn-group {
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				margin-top: 30rpx;

				.paste {
					width: 40%;
				}

				.get {
					width: 56%;
				}
			}
		}
	}

	.bg {
		position: fixed;
		top: 0rpx;
		left: 0rpx;
		width: 100%;
		background-color: #26B3A0;
		min-height: 200rpx;
		border-radius: 0rpx 0rpx 140rpx 140rpx;
		z-index: -1;
	}
</style>
